}

.btn {
  width: 90%;
  margin-left: 5%;
  padding: 10px;
  cursor: pointer;
  font-size: 1.3em;
  /* text-decoration: underline; */
  color: pink;
  margin-bottom: 85px;
}

.btn:focus {
  outline: 0;
}

.btn:hover {
  color: pink;
  background-color: transparent;
  text-decoration: underline;
}
.btn:active {
    color: pink;
    background-color: transparent;
    text-decoration: none;
    border: none;
}

