.spinner-wrapper {
  /* position: fixed; */
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.spinner-wrapper .spinner {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 4.5rem;
  height: 4.5rem;
  font-family: "Oswald";
  font-size: 1rem;
  color: gainsboro;
}
.spinner-wrapper .spinner .fragment {
  display: block;
  position: absolute;
  box-sizing: border-box;
  width: 4.5rem;
  height: 4.5rem;
  border: 0.25rem solid transparent;
  border-top: 0.25rem solid #8dc63f;
  border-radius: 50%;
  mix-blend-mode: screen;
  -webkit-animation: rotate 2.5s linear infinite;
          animation: rotate 2.5s linear infinite;
}
.spinner-wrapper .spinner .fragment:first-of-type {
  -webkit-animation: rotate 1.5s ease-in-out infinite;
          animation: rotate 1.5s ease-in-out infinite;
  border-top-color: #00aeef;
}
.spinner-wrapper .spinner .fragment:last-of-type {
  -webkit-animation: rotate 2s ease-in-out infinite;
          animation: rotate 2s ease-in-out infinite;
  border-top-color: #ec008c;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

